const app = {
  state: {
    device: 'desctop'
  },
  mutations:{
    UPDATE_DEVICE(state,device) {
      state.device = device
    }
  },
  actions: {
    updateDevice(context) {
      context.emit('UPDATE_DEVICE')
    }
  }
}

export default app


