import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/view/HelloWorld'
import layout from '@/components/view/Layout/main'
import page1 from '@/components/view/jobs/page1'
import page2 from '@/components/view/jobs/page2'
import errorPage from '@/components/view/jobs/404'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: HelloWorld
    },
    {
      path: '/layout',
      component: layout
    },
    {
      path: '/page',
      component: layout,
      meta:{name:'主页面', icon: 'personnel'},
      children:[
        {
          path: 'page1',
          component: page1,
          meta:{name:'页面一', icon: 'personnel'},
        },
        {
          path: 'page2',
          component: page2,
          meta:{name:'页面二', icon: 'personnel'},
        }
      ]
    },
    {
      path: '*',
      component: errorPage
    }
  ]
})



